import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import SendIcon from '@material-ui/icons/Send';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
  customLabel:{
    color:'#1799e2 !important'
  },
  cssOutlinedInput: {
    '&$cssFocused $notchedOutline': {
      borderColor: '#1799e2 !important',
    }
  },

  cssFocused: {},

  notchedOutline: {
    borderWidth: '1px',
    borderColor: 'rgba(0,0,0,.5) !important'
  },
  cssButton:{
    fontWeight:"500",
    fontSize:"1.2rem",
    '@media(min-width:725px)':{
      fontSize:"1.1rem"
    },
    backgroundColor:'#22C5F2',
    color:'#fff',
    '&:hover': {
      backgroundColor: '#1799E2',
      borderColor: '#1799E2',
      boxShadow: 'none',
    },
    '&:active': {
      boxShadow: 'none',
      backgroundColor: '#1dafd7',
      borderColor: '#1dafd7',
    },
    '&:focus': {
      boxShadow: 'none',
    }
  }
}));

export default function FullWidthGrid() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <Grid container spacing={4} justify="center">
        <Grid item xs={12}>
            <TextField id="outlined-basic"  InputLabelProps={{
            classes: {
              root: classes.customLabel,
              focused: classes.customLabel
            },
          }} 
          InputProps={{
            classes: {
              root: classes.cssOutlinedInput,
              focused: classes.cssFocused,
              notchedOutline: classes.notchedOutline
            }
          }} 
          label="Name" variant="outlined" fullWidth={true} />
        </Grid>
        <Grid item xs={12} sm={12}>
          <TextField id="outlined-basic"  InputLabelProps={{
              classes: {
                root: classes.customLabel,
                focused: classes.customLabel
              },
            }} 
            InputProps={{
              classes: {
                root: classes.cssOutlinedInput,
                focused: classes.cssFocused,
                notchedOutline: classes.notchedOutline
              },
              type:"email"
            }} 
            label="Email" variant="outlined" fullWidth={true} 
          />
        </Grid>
        <Grid item xs={12} sm={12}>
          <TextField id="outlined-basic"  InputLabelProps={{
              classes: {
                root: classes.customLabel,
                focused: classes.customLabel
              },
            }} 
            InputProps={{
              classes: {
                root: classes.cssOutlinedInput,
                focused: classes.cssFocused,
                notchedOutline: classes.notchedOutline
              }
            }} 
            label="Subject" variant="outlined" fullWidth={true} 
          />
        </Grid>
        <Grid item xs={12} sm={12}>
          <TextField id="outlined-basic"  InputLabelProps={{
              classes: {
                root: classes.customLabel,
                focused: classes.customLabel
              },
            }} 
            InputProps={{
              classes: {
                root: classes.cssOutlinedInput,
                focused: classes.cssFocused,
                notchedOutline: classes.notchedOutline
              }
            }} 
            label="Message" variant="outlined" fullWidth={true} multiline={true}
          />
        </Grid>
        <Grid item xs={5} sm={5}>
          <Button size="large" variant="contained" color="#000" className={classes.cssButton} fullWidth={true} endIcon={<SendIcon />}>send</Button>
        </Grid>
      </Grid>
    </div>
  );
}
