import React from 'react';

const Client = () => {
    return ( 
        <div className="client" data-aos="flip-left">
                <img src="assets/chick_logo.png" alt="client_logo" />
        </div>
     );
}

const Clients = () => {
    return ( 
    <section className="clients" id="clients">
        <span data-aos="fade-up">
            <h1 className="sec-title">Our Clients</h1>
            <p className="sec-desc">Lorem ipsum dolor sit amet consectetur, adipisicing elit. Fugiat, nostrum!</p>
        </span>
        <div className="clients-container">
            <Client />
            <Client />
            <Client />
            <Client />
            <Client />
            <Client />
            <Client />
            <Client />
        </div>
    </section>
     );
}

export default Clients;