import React,{useState,useEffect} from "react";
import {Link} from "react-scroll";

const Header = () => {
    const [isToggled,setIsToggled] = useState(false);
    let navLinks = isToggled ? "nav-links nav-active" : "nav-links";
    let hambMenu = isToggled ? "hamburger-menu bar-change" : "hamburger-menu";
    useEffect(() => {
        window.addEventListener('scroll',() => {
            const navbar = document.querySelector('.navbar');
            if(navbar){
            navbar.classList.toggle('sticky', window.scrollY > 0);
            }   
            // console.log(window.scrollY);
            // if(window.scrollY > 0){
            //     setIsScrolled(prevVal => !prevVal)
            // }
        });
    })
    function handleClick() {
        setIsToggled(prevVal => !prevVal);
    }
    return ( <header className="header">
    <nav className="navbar">
        <h2 className="logo">WebAgent</h2>
        <ul className={navLinks}>
            <li><a href="/">Home</a></li>
            <li>
                <Link to="services" activeClass="active" smooth={true} duration={1500}>
                    Services
                </Link>
            </li>
            <li>
                <Link to="portfolio" activeClass="active" smooth={true} duration={1500}>
                    Portfolio
                </Link>
            </li>
            <li>
                <Link to="about" activeClass="active" smooth={true} duration={1500}>
                    About Us
                </Link>
            </li>
            <li>
                <Link to="contact" activeClass="active" smooth={true} duration={1500}>
                    Contact
                </Link>
            </li>
        </ul>
        <div className={hambMenu} onClick={handleClick}>
            <div className="bar1"></div>
            <div className="bar2"></div>
            <div className="bar3"></div>
        </div>
    </nav>
    <div className="showcase">
        <svg id="hero-bg" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 278.62 552.65"><defs><linearGradient id="linear-gradient" x1="371.47" y1="934.84" x2="404.56" y2="837.01" gradientTransform="translate(1576.45 944.38) rotate(136.21)" gradientUnits="userSpaceOnUse"><stop offset="0" stopColor="#2aeaff" stopOpacity="0"/><stop offset="1" stopColor="#1799e2"/></linearGradient><linearGradient id="linear-gradient-2" x1="576.35" y1="728.95" x2="617.75" y2="606.54" gradientTransform="translate(1576.45 340.38) rotate(136.21)" gradientUnits="userSpaceOnUse"><stop offset="0" stopColor="#2aeaff"/><stop offset="1" stopColor="#1799e2" stopOpacity="0"/></linearGradient></defs><title>squares-inf</title><g id="smSqr"><path d="M794.33,575.05l-109-113.69-113.69,109L680.64,684Zm-188.63-4,79.87-76.55,76.55,79.87-79.87,76.55Z" transform="translate(-543.69 -131.39)" fill="url(#linear-gradient)"/></g><g id="bgSqr"><path d="M822.31,273.65,685.95,131.39,543.69,267.75,680.05,410Zm-236-5,99.93-95.79L782,272.79l-99.93,95.79Z" transform="translate(-543.69 -131.39)" fill="url(#linear-gradient-2)"/></g></svg>
        <span className="show-content">
        <h1>Lorem ipsum dolor sit amet consectetur.</h1>
        <button><a href="#clients">See More</a></button>
        </span>
    </div>
</header> );
}
 
export default Header;