import React from 'react';
import Form from "./Form";
import Info from "./Info";

const Contact = () => {
    return (
        <section className="contact" id="contact">
            <span  data-aos="fade-up">
                <h1 className="sec-title">Contact Us</h1>
                <p className="sec-desc">Lorem ipsum dolor sit amet consectetur, adipisicing elit. Fugiat, nostrum!</p>
            </span>
            <div className="contact-container">
                <div className="form" data-aos="zoom-in-up">
                    <Form />
                </div>
                <div className="info" data-aos="zoom-in-up">
                    <Info />
                </div>
            </div>
        </section>
     );
}
 
export default Contact;